/* eslint-disable @typescript-eslint/no-misused-promises */
import * as vscode from 'vscode'
import { Logger } from './logger'
import { fs } from '../utils/fs'
import { path } from '../utils/path'
import isEqual = require('licia/isEqual')
class ResolveAliasHandler implements IExtensionCore {
  private watcherList: vscode.FileSystemWatcher[] = []
  private watcherHandlerList: vscode.Disposable[] = []
  private readonly cacheMap: Map<string, IAppJSON | null> = new Map()
  constructor (
    private readonly context: vscode.ExtensionContext,
    private readonly logger: Logger
  ) {
  }

  init (): void {
    const workspaceFolders = vscode.workspace.workspaceFolders
    workspaceFolders?.forEach(async workspace => {
    // add watcher
      const watcher = vscode.workspace.createFileSystemWatcher(
        new vscode.RelativePattern(workspace, '**/app.json')
      )
      this.watcherList.push(watcher)
      this.watcherHandlerList.push(
        watcher.onDidChange(async (uri) => {
          if (this.cacheMap.has(uri.fsPath)) {
            const cache = this.cacheMap.get(uri.fsPath)
            const current = await fs.readJSON<IAppJSON>(uri)
            if (!isEqual(cache?.resolveAlias, current?.resolveAlias)) {
              this.cacheMap.set(uri.fsPath, current)
              this.logger.log('get different resolveAlias, reload ts server')
              // reload ts server
              vscode.commands.executeCommand('typescript.restartTsServer')
            }
          } else {
          // do not exists in cache, add
            this.logger.log('get new resolveAlias, reload ts server')
            this.setAppJSONCache(uri)
            // reload ts server
            vscode.commands.executeCommand('typescript.restartTsServer')
          }
        })
      )
      const projectPath = workspace.uri
      const projectConfigPath = path.join(projectPath, 'project.config.json')
      try {
        const projectConfig = await fs.readJSON<IProjectConfig>(projectConfigPath)
        if (projectConfig !== null) {
          const realMiniprogramRoot = path.join(
            projectPath,
            projectConfig.srcMiniprogramRoot ?? projectConfig.miniprogramRoot ?? '.'
          )
          const appJSONPath = path.join(realMiniprogramRoot, 'app.json')
          this.setAppJSONCache(appJSONPath)
        }
      } catch (e: any) {
        this.logger.log(e)
      }
    })
    this.logger.log('init resolveAliasHandler done')
  }

  private async setAppJSONCache (uri: vscode.Uri): Promise<void> {
    const appJSONPath = uri
    const appJSONPathExists = await fs.isExists(appJSONPath)
    if (appJSONPathExists) {
      const appJSON = await fs.readJSON<IAppJSON>(appJSONPath)
      this.cacheMap.set(appJSONPath.fsPath, appJSON)
    }
  }

  dispose (): void {
    this.watcherList.forEach(item => item?.dispose())
    this.watcherHandlerList.forEach(item => item?.dispose())
    this.watcherList = []
    this.watcherHandlerList = []
    this.cacheMap.clear()
    this.logger.log('dispose resolveAliasHandler done')
  }

  restart (): void {
    this.dispose()
    this.init()
    this.logger.log('restart resolveAliasHandler done')
  }
}

export {
  ResolveAliasHandler
}
