import * as vscode from 'vscode'

class FS {
  constructor (
    readonly fs: typeof vscode.workspace.fs
  ) {
  }

  async isExists (uri: vscode.Uri | string): Promise<boolean> {
    if (typeof uri === 'string') {
      uri = vscode.Uri.parse(uri)
    }
    return await this.fs.stat(uri).then(() => true, () => false)
  }

  async readFileAsString (uri: vscode.Uri | string): Promise<string> {
    if (typeof uri === 'string') {
      uri = vscode.Uri.parse(uri)
    }
    const res = await this.fs.readFile(uri)
    return Buffer.from(res).toString('utf-8')
  }

  async readJSON<T> (uri: vscode.Uri | string): Promise<T | null> {
    try {
      if (await this.isExists(uri)) {
        const res = await this.readFileAsString(uri)
        return JSON.parse(res)
      }
    } catch (e) {
    }
    return null
  }
}

export const fs = new FS(vscode.workspace.fs)
