import { ExtensionContext, window } from 'vscode'
import * as dayjs from 'dayjs'
const LOG_CHANNEL = 'WeChat MiniProgram Extension'
class Logger {
  private _init = false
  private readonly channel = window.createOutputChannel(LOG_CHANNEL)
  constructor (
    private readonly context: ExtensionContext
  ) {
    this.init()
  }

  private init (): void {
    if (this._init) {
      return
    }
    this._init = true
    this.context.subscriptions.push(this.channel)
  }

  private transformLogs (logs: Array<string | number | Object>): string {
    return logs.map(log => {
      if (typeof log === 'object') {
        return JSON.stringify(log, null, 2)
      }
      return log
    }).join(' ')
  }

  log (...logs: Array<string | number | Object>): void {
    console.log(...logs)
    this.channel.appendLine(`[${dayjs().format('YYYY-MM-DD HH:mm:ss')}] ${this.transformLogs(logs)}`)
  }
}

export {
  Logger
}
