import { Logger } from './logger'
import { fs } from '../utils/fs'
import * as path from 'path'
import * as vscode from 'vscode'
import {
  LanguageClient,
  LanguageClientOptions,
  ServerOptions,
  TransportKind
} from 'vscode-languageclient/node'
import { SEND_TO_DEVTOOLS_ID, SEND_TO_SKYLINE_LSP_SERVER_ID } from '../lib/constants'

class SkylineStyleClient implements IExtensionCore {
  private skylineStyleClient: LanguageClient | null = null
  constructor (
    private readonly context: vscode.ExtensionContext,
    private readonly logger: Logger
  ) {
  }

  private sendToDevtools (params: ISendToDevtoolsParams): void {
    vscode.commands.executeCommand(SEND_TO_DEVTOOLS_ID, params)
  }

  private sendToServer (params: ILSPNotificationData): void {
    this.onReady(() => {
      this.skylineStyleClient?.sendNotification(SEND_TO_SKYLINE_LSP_SERVER_ID, params)
    })
  }

  private onReady (fn: Function): void {
    this.skylineStyleClient?.onReady().then(() => {
      fn()
    })
  }

  init (): void {
    this.sendToDevtools({
      command: 'getFPCCPath'
    })
    vscode.commands.registerCommand('wechatMiniprogramDevelopment.getFPCCPath', async (data: { fpccPath: string }) => {
      const fpccPath = data.fpccPath
      this.logger?.log(`fpccPath: ${fpccPath}`)
      if (fpccPath !== '' && await fs.isExists(fpccPath)) {
        // init LSP client\server
        const serverModule = this.context.asAbsolutePath(
          path.join('skylineStyleServer.js') // in wechat devtools, there is not a 'out' folder
        )

        const serverOptions: ServerOptions = {
          run: {
            module: serverModule,
            transport: TransportKind.ipc,
            options: {
              env: {
                FPCC_PATH: fpccPath
              }
            }
          },
          debug: {
            module: serverModule,
            transport: TransportKind.ipc,
            options: {
              env: {
                FPCC_PATH: fpccPath
              }
            }
          }
        }
        const clientOptions: LanguageClientOptions = {
          documentSelector: [{
            scheme: 'file',
            language: 'css',
            pattern: '**/*.wxss' // detect wxss
          }]
        }
        this.skylineStyleClient = new LanguageClient(
          'skylineStyleServer',
          'Skyline Style Server',
          serverOptions,
          clientOptions
        )
        this.skylineStyleClient.start()
        this.onReady(() => {
          this.logger?.log('skylineStyleLanguage server start')
          this.skylineStyleClient?.onNotification(SEND_TO_SKYLINE_LSP_SERVER_ID, (payload: ILSPNotificationData) => {
            this.logger?.log('received payload', payload)
            switch (payload.command) {
              case 'checkIsSkylineFile': {
                this.sendToDevtools(payload)
              }
            }
          })
        })
      }
    })
    vscode.commands.registerCommand('wechatMiniprogramDevelopment.checkIsSkylineFile', (result: { path: string, result: boolean }) => {
      this.sendToServer({
        command: 'checkIsSkylineFile',
        data: result
      })
    })
  }

  dispose (): void {
    this.skylineStyleClient?.stop()
  }
}

export {
  SkylineStyleClient
}
