// The module 'vscode' contains the VS Code extensibility API
// Import the module and reference it with the alias vscode in your code below
import * as vscode from 'vscode'
import { Logger } from './core/logger'
import { ResolveAliasHandler } from './core/handle-resolve-alias'
import { RESTART_COMMAND } from './utils/constants'
import { SkylineStyleClient } from './core/handle-skyline-wxss-warning'
import { SEND_TO_DEVTOOLS_ID } from './lib/constants'

class WechatMiniProgramExtension {
  private resolveAliasHandler: ResolveAliasHandler | null = null
  private skylineStyleClient: SkylineStyleClient | null = null
  private logger: Logger | null = null
  activate (context: vscode.ExtensionContext): void {
    this.logger = new Logger(context)
    this.logger.log('now is activate')
    this.resolveAliasHandler = new ResolveAliasHandler(context, this.logger)
    this.skylineStyleClient = new SkylineStyleClient(context, this.logger)
    this.resolveAliasHandler.init()
    this.skylineStyleClient.init()
    this.registerCommand(context)
  }

  sendToDevtools (params: ISendToDevtoolsParams): void {
    vscode.commands.executeCommand(SEND_TO_DEVTOOLS_ID, params)
  }

  registerCommand (context: vscode.ExtensionContext): void {
    context.subscriptions.push(vscode.commands.registerCommand(RESTART_COMMAND, () => {
      this.resolveAliasHandler?.restart()
    }))
  }

  deactivate (): void {
    this.resolveAliasHandler?.dispose()
    this.skylineStyleClient?.dispose()
  }
}

const extension = new WechatMiniProgramExtension()

export function activate (context: vscode.ExtensionContext): void {
  extension.activate(context)
}

export function deactivate (): void {
  extension.deactivate()
}
